import React from "react";
import './home.css';
import myself from '../../resources/pictures/me.png'
import myself512x512 from '../../resources/pictures/me512x512.png'
import myself2x3 from '../../resources/pictures/me2x3.png'

const Home = () => {
    return (
        <div className='homeContent'>
            <div className='imageContainer'>
                <img src={myself512x512}/>
            </div>
            <div>
                <h2>Kurzprofil</h2>
                <p>Ich bin Florian Herrmannsdörfer oder kurz Flo. Meinen Einstieg in die Welt der Computer und
                    Informatik fand ich durch meinen ersten eigenen Computer. Vorerst beschränkten sich meine Interessen
                    auf den Hardware- und Netzwerkbereich, z.B. Ausbau des Heimnetzwerkes, Hardwareupgrades an meinem
                    Computer oder hosten von Servern für eine eigene Cloud oder für Spiele, um mit meinen Freunden zu
                    spielen. Das Programmiren habe ich in der Fachoberschule Bayreuth gelernt, als es Bestandteil des
                    Unterrichtes war.
                </p>
            </div>
        </div>
    );
}

export default Home;
