import React from "react";
import './navbar.css'
import {Link} from "react-router-dom";

const Navbar = () => {
    return (
        <nav className="navbar">
            <ul>
                <li className="nav-item">
                    <Link to="/">
                        Home
                    </Link>
                </li>
                <li className="nav-item">
                    <Link to="/aboutMe">
                        Über mich
                    </Link>
                </li>
                <li className="nav-item">
                    <Link to="/resume">
                        Lebenslauf
                    </Link>
                </li>
            </ul>
        </nav>
    )
}

export default Navbar;
