import React from "react";
import './aboutMe.css';

const AboutMe = () => {
    return (
        <div className='aboutMeContent'>

            <div>
                <h2>
                    Über mich
                </h2>
                <p>
                    Ich komme aus einem kleinen Dorf namens Alladorf. Dort lebe ich mit meiner Mutter, unseren beiden
                    Hunden und meinen beiden Katzen. In meiner Freizeit spiele ich gerne Computerspiele, allein mit
                    Musik oder zusammen mit Freunden. Gerne genieße ich auch die Natur bei einem Spaziergang, natürlich
                    mit den Hunden. Sehr gerne koche ich auch, am besten frisch und so viel wie möglich selbst gemacht.
                    Zu meinen Spezialitäten gehören Burger mit selbst gebackenen Buns und Salat und Gurken aus dem
                    eigenen Garten.

                </p>
            </div>

            <div>
                <h2>
                    Projekte
                </h2>
                <h3>Azul</h3>
                <p>
                    Dieses Projekt war Teil einer Gruppenstudienarbeit, bestehend aus einem Team von vier Leuten, im
                    Fach Software Engineering II. Die Aufgabe war es, dass Brettspiel Azul, gemäß den offiziellen Regeln
                    zu Implementieren. Zudem sollte es auch noch die Möglichkeit geben Spiele zu speichern und laden.
                    <br/>
                    Das Projekt wurde mit dem Vorgehensmodell von Scrum durchgeführt. Die Software wurde mit Java
                    implementiert.

                </p>
                <h3>Partyplaner</h3>
                <p>
                    Ziel des Projektes war, eine Software zu entwickeln, die Unterstützung bietet, bei der Planung von
                    Partys. Die Software bot Funktionen für, anlegen einer Party, Aufgabenverwaltung, Helferverwaltung,
                    Schichtplanung, Budgetplanung, Archiviren von Partys mit Bildern.
                    <br/>
                    Das Projekt wurde agile mit Scrum entwickelt und umgesetzt wurde es mit Java Spring.

                </p>
                <h3>Stundenplanverwaltung</h3>
                <p>
                    Ziel des Projektes war, eine Webanwendung zu entwickeln, die die Möglichkeit bietet, den Stundenplan
                    anzeigen zu lassen zu Ausgewählten Studienfach und Semester.
                    <br/>
                    Das Projekt wurde mit Laravel umgesetzt.
                </p>
                <h3>Persönliche Website</h3>
                <p>
                    Entwicklung einer Webseite, die als Vorstellung meiner Person, Erfahrungen und Projekten dient. Die
                    Webanwendung wurde mit React entwickelt und wird mit Github Pages gehostet.

                    <h4>Zukünftige Pläne:</h4>

                    <ul>
                        <li>
                            Hosting auf eigenem Server
                        </li>
                        <li>
                            Authentifizierung mit Rollenkonzept
                        </li>
                        <li>
                            Möglichkeit die Sprache auf Englisch einzustellen
                        </li>
                    </ul>
                </p>
            </div>

        </div>

    );
}

export default AboutMe;
